<template>
  <div id="reading" class="container">
     <div class="search-wrap"><van-search shape="round" v-model="searchValue" placeholder="搜索素材名称" /></div>
     <div class="resources-categories-wrap">
        <van-tabs v-model="categoryActive" color='#1989fa' animated swipeable v-if="categories.length>0">
          <van-tab :title="item.name" v-for="(item, index)  in categories" :key="index">
              <van-grid  :border='false' :column-num='4' clickable>
                <van-grid-item class='category-item' :icon='ctg.img' :text='ctg.name' v-for="ctg in item.subCategories" :key="ctg.id" @click="goResources(ctg.id)"/>
              </van-grid>
          </van-tab>
        </van-tabs>
    </div>
    <div class="resources-list-wrap">
        <van-tabs v-model="tagActive" color="#1989fa" title-active-color="#1989fa" close-icon="arrow-left" animated swipeable>
          <van-tab :title="item.name" v-for="item in tagsToFetch" :key="item.name">
              <van-pull-refresh v-model="item.status.refreshing" @refresh="onRefresh(item)">
                  <van-list
                    v-model="item.status.loading"
                    :finished="item.status.finish"
                    finished-text="没有更多了"
                    error-text="请求失败，点击重新加载"
                    :error.sync="item.status.error"
                    :offset="100"
                    @load="getMyResources(item)">
                       <resource-item v-for="res in item.resources" :key="item.tag+'_'+res.id+Math.random()*1000" :resource="res"/>
                  </van-list>
              </van-pull-refresh>
          </van-tab>
        </van-tabs>
    </div>
  </div>
</template>

<script>

import { Search, Tab, Tabs, Grid, GridItem, PullRefresh, List } from 'vant';
import {getResourceCategories, getResourceList} from '@/api/resource'
import resourceItem from '@/views/resources/resourceItem'
export default {
  name: 'reading',
  components: { [Search.name]:Search, [Tab.name]:Tab, [Tabs.name]:Tabs,[Grid.name]:Grid, [GridItem.name]:GridItem, [PullRefresh.name]:PullRefresh, [List.name]:List, resourceItem },
  data() {
    return {
      searchValue:'',
      categoryActive:0,
      tagActive:0,
      categories:[],
      tagsToFetch:[{name:"热门素材", tag:"hot"},{name:"我的原创", tag:"original"},{name:"我的收藏", tag:"favorite"}], //"hot", "original", "favorite"
      defaultStatus: {
            currentPage: 0,
            error:false, 
            finish: false,
            loading: false,
            refreshing: false,
      },
    }
  },
  created(){
    let tags = this.tagsToFetch
    for(let i=0; i<tags.length; i++){
        this.$set(tags[i], "status", Object.assign({}, this.defaultStatus))
    }
    this.getCategories()
  },
  methods:{
    goResources(id){
      this.$router.push({path:'/resources/'+id})
    },
    getCategories(){
      return new Promise((resolve, reject) => {
        getResourceCategories().then(response => {
          this.categories = response.data
          resolve()
        }).catch(error => {
           console.log(error)
           reject(error)
        })
      })
    },
    getMyResources( tagItem ){
      return new Promise((resolve, reject) => {
        let status = tagItem.status
        status.currentPage = status.currentPage+1
        getResourceList({tag:tagItem.tag, page: status.currentPage}).then(response => {
          const { data } = response
          if(data.empty && data.empty==true){
             status.finish = true;
          }else{
              if( 1 == status.currentPage || !tagItem.resources){
                  this.$set(tagItem, "resources", data.resources)
              }else{
                tagItem.resources = tagItem.resources.concat(data.resources)
              }
          }
          status.loading = false
          resolve()
          }).catch(error => {
              status.currentPage = status.currentPage-1
              status.error = true
              status.loading = false
              status.errorMsg =  error
              console.log(error)
              reject(error)
          })
        
      })
    },
    onRefresh( tagItem ){
      let status = tagItem.status
      status.currentPage = 0
      this.getMyResources( tagItem ).then(()=>status.refreshing=false).catch(()=>status.refreshing=false)
    }
  }
}
</script>


<style lang="scss" scoped>
.resources-categories-wrap{
    ::v-deep .van-tab{
      flex: inherit;
    }
    ::v-deep .van-tabs__nav{
      padding-left:20px;
      padding-right:20px;
      .van-tab{
        padding:0 20px;
      }
    }
    ::v-deep .van-tab--active{
      font-size:32px;
    }
    ::v-deep .van-tabs__content{
      border-top:1px solid #ddd;
      margin:0 20px
    }
    .category-item {
      text-align:center;
      ::v-deep .van-grid-item__text{
        color:#000;
        font-size:30px;
      }
      ::v-deep .van-icon{
          width:80%;
      }
      ::v-deep .van-icon__image{
        width:100%;
        height:auto;
        border-radius:5px;
      }
  }
}
.resources-list-wrap{
  padding-top:20px;
  background-color: #eee;
  ::v-deep .van-tabs__wrap{
     border-bottom: 1px solid #eee;
   }
}
</style>
